<template>
  <div class="container-fluid">
    <div class="row mt-3">
      <div class="col-3">
        <div class="card">
          <div class="card-header">
            Tasks
          </div>
          <ul class="list-group list-group-flush">
            <!-- Tasks Headers here -->
            <li class="list-group-item pointer" v-for="task in tasks.value" :key="task.id"
              @click="select_task_by_id(task.id)">{{task.id}}</li>
          </ul>
        </div>
      </div>
      <div class="col-9">
        <!-- Tasks cards Here -->
        <div class="container">
          <TaskCard :is="selected_task" :id="selected_task.id" :struct="selected_task.struct"
            :active="selected_task.active" />
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed } from "vue";
import io from 'socket.io-client';
import TaskCard from './components/TaskCard.vue';

const socket = io({
  extraHeaders: {
    'type': 'web'
  }
})

const tasks = reactive({
  value: [{
    "id": "Welcome to VSUI",
    "struct":
      [{ "key": "Welcome to VSUI", "component": "LogElement", "data": ['no data loaded'] }],
    "active": false
  }]
})
const selected_task_index = ref(0)

const selected_task = computed(() => tasks.value[selected_task_index.value])
function select_task_by_id(target_id) {
  selected_task_index.value = tasks.value.findIndex(x => x.id === target_id);
}

socket.on('connect', () => {
  console.log('connected')
})

socket.on('data_push', (data) => {
  if (data.data == JSON.parse('No Tasks')) {
    console.log('no tasks at the moment')
    tasks.value = [{
      "id": "Welcome to VSUI",
      "struct":
        [{ "key": "Welcome to VSUI", "component": "LogElement", "data": ['no data loaded'] }],
      "active": false
    }]
  } else {
    tasks.value = JSON.parse(data.data);
    console.log(tasks.value)
  }
}
)
</script>

<style>
li.pointer {
  cursor: pointer
}
</style>
